<template>

  <div>

    <div class="button-box">
      <!--  -->
      <n-button @click="showBuildingModal = true;buildingStatus = false;">
        <template #icon>
          <n-icon>
            <add-icon />
          </n-icon>
        </template>
        
      </n-button>
      <n-button @click="showBuildingModal = true;buildingStatus = true;">
        <template #icon>
          <n-icon>
            <edit-icon />
          </n-icon>
        </template>
        
      </n-button>
      <n-button @click="deleteBuilding">
        <template #icon>
          <n-icon>
            <delete-icon />
          </n-icon>
        </template>
        
      </n-button>
      <!--  -->
      <n-button @click="showApartmentModal = true;apartmentStatus = false;">
        <template #icon>
          <n-icon>
            <add-icon />
          </n-icon>
        </template>
        
      </n-button>
      <n-button @click="showApartmentModal = true;apartmentStatus = true;">
        <template #icon>
          <n-icon>
            <edit-icon />
          </n-icon>
        </template>
        
      </n-button>
      <n-button @click="deleteApartment">
        <template #icon>
          <n-icon>
            <delete-icon />
          </n-icon>
        </template>
        
      </n-button>
      <!--  -->
      <n-button @click="openHousingModel(false)" type="primary">
        <template #icon>
          <n-icon>
            <add-icon />
          </n-icon>
        </template>
        
      </n-button>
      <!--  -->
      <n-button @click="openImportProperty" type="primary">
        <template #icon>
          <n-icon>
            <add-icon />
          </n-icon>
        </template>
        
      </n-button>
    </div>

    <div class="main">
      <div class="left">
        <n-card class="left-card">
          <n-spin :show="leftSpin">
            <n-tree :node-props="nodeProps" block-line :data="buildingData" default-expand-all virtual-scroll />
          </n-spin>
        </n-card>
      </div>

      <div class="right">

        <n-spin :show="mainSpin">

          <n-card title="">
            <n-space item-style="display: flex; align-item: center;">
              <n-input v-model:value="housingNo" style="width:256px" type="text" placeholder="" />
              <n-select v-model:value="housingStatus" placeholder="" style="width:160px"
                :options="statusOptions" />
              <n-select v-model:value="housingType" :loading="housingTypeLoading" placeholder=""
                style="width:160px" :options="housingOptions" />
              <n-button @click="findClick" type="primary">
                <template #icon>
                  <n-icon>
                    <search-icon />
                  </n-icon>
                </template>
                
              </n-button>
              <n-button @click="resetClick">
                <template #icon>
                  <n-icon>
                    <arrow-clockwise-icon />
                  </n-icon>
                </template>
                
              </n-button>
            </n-space>
          </n-card>

          <n-table style="margin-top: 10px" striped>
            <thead>
              <tr>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
              </tr>
            </thead>
            <tbody v-if="houseDataDisplay">
              <tr>
                <td colspan="11" style="text-align:center;"></td>
              </tr>
            </tbody>
            <tbody v-else>
              <tr v-for="(item,index) in houseData.list" :key="index">
                <td>{{item.building != null ? item.building.buildingNo : ''}}-{{item.unit != null ? item.unit.unitNo :
                ''}}-{{item.housesNo}}</td>
                <td>{{item.floor}}</td>
                <td>{{item.owner != null ? item.owner.name : ''}}</td>
                <td>{{item.owner != null ? item.owner.phone : ''}}</td>
                <td>{{item.housesType != null ? item.housesType.name : ''}}</td>
                <td>{{item.building != null ? item.building.area : ''}} </td>
                <td>{{item.area}} </td>
                <td>{{item.checkTime}}</td>
                <td>{{item.status === 0 ? '' : ''}}</td>
                <td>{{item.exquisite === 0 ? '' : ''}}</td>
                <td>
                  <n-button-group size="large">
                    <n-button @click="openHousingModel(true,item)" size="small"></n-button>
                    <n-button @click="deleteHousing(item)" type="error" size="small" strong secondary></n-button>
                  </n-button-group>
                </td>
              </tr>
            </tbody>
          </n-table>

          <div v-if="!(pages <= 1)"
            style="display: flex;justify-content: center;align-items: center;margin-top: 10px;margin-bottom: 10px;">
            <n-pagination @update:page="pageNoUpdate" v-model:page="pageNo" :page-count="pages" />
          </div>

        </n-spin>

      </div>
    </div>

    <!--  -->
    <n-modal :auto-focus="false" v-model:show="showBuildingModal" class="custom-card" preset="card" style="width: 600px"
      :title="(buildingStatus ? '' : '') + ''" transform-origin="center" size="huge" :bordered="false"
      :segmented="{content: 'soft',footer: 'soft'}">
      <n-form ref="buildingFormRef" label-placement="left" :label-width="80" :model="buildingValue"
        :rules="buildingRules" size="medium">
        <n-grid cols="1">
          <n-grid-item>
            <n-form-item label="" path="id">
              <n-input v-model:value="buildingValue.id" placeholder="" />
            </n-form-item>
            <n-form-item label="" path="name">
              <n-input v-model:value="buildingValue.name" placeholder="" />
            </n-form-item>
            <n-form-item label="" path="area">
              <n-input v-model:value="buildingValue.area" placeholder="" />
            </n-form-item>
            <n-form-item label="" path="remark">
              <n-input type="textarea" v-model:value="buildingValue.remark" placeholder="" />
            </n-form-item>
          </n-grid-item>
        </n-grid>
      </n-form>
      <template #footer>
        <n-space justify="end">
          <n-button @click="showBuildingModal = false"></n-button>
          <n-button :loading="submitLoading" type="primary" attr-type="button" @click="buildingSubmit"></n-button>
        </n-space>
      </template>
    </n-modal>

    <!--  -->
    <n-modal :auto-focus="false" v-model:show="showApartmentModal" class="custom-card" preset="card"
      style="width: 600px" :title="(apartmentStatus ? '' : '') + ''" transform-origin="center" size="huge"
      :bordered="false" :segmented="{content: 'soft',footer: 'soft'}">
      <n-form ref="apartmentFormRef" label-placement="left" :label-width="80" :model="apartmentValue"
        :rules="apartmentRules" size="medium">
        <n-grid cols="1">
          <n-grid-item>
            <n-form-item label="" path="id">
              <n-input v-model:value="apartmentValue.id" placeholder="" />
            </n-form-item>
            <n-form-item label="" path="totalNumberOfLayers">
              <n-input-number style="width:100%" v-model:value="apartmentValue.totalNumberOfLayers" clearable />
            </n-form-item>
            <n-form-item label="">
              <n-switch v-model:value="apartmentValue.elevator">
                <template #checked>
                  
                </template>
                <template #unchecked>
                  
                </template>
              </n-switch>
            </n-form-item>
            <n-form-item label="" path="remark">
              <n-input type="textarea" v-model:value="apartmentValue.remark" placeholder="" />
            </n-form-item>
          </n-grid-item>
        </n-grid>
      </n-form>
      <template #footer>
        <n-space justify="end">
          <n-button @click="showApartmentModal = false"></n-button>
          <n-button :loading="apartmentSubmitLoading" type="primary" attr-type="button" @click="apartmentSubmit">
          </n-button>
        </n-space>
      </template>
    </n-modal>

    <!--  -->
    <n-modal :auto-focus="false" v-model:show="showHousingModal" class="custom-card" preset="card" style="width: 600px"
      :title="(housingModelStatus ? '' : '') + ''" transform-origin="center" size="huge" :bordered="false"
      :segmented="{content: 'soft',footer: 'soft'}">
      <n-form ref="housingFormRef" label-placement="left" :label-width="120" :model="housingValue" :rules="housingRules"
        size="medium">
        <n-grid cols="1">
          <n-grid-item>
            <n-form-item label="" path="housesNo">
              <n-input v-model:value="housingValue.housesNo" placeholder="" />
            </n-form-item>
            <n-form-item label="" path="floor">
              <n-input-number style="width:100%" v-model:value="housingValue.floor" placeholder="" clearable />
            </n-form-item>
            <n-form-item v-show="housingModelStatus" label="" path="unitNo">
              <n-select :loading="unitOptionLoading" v-model:value="housingValue.unitNo" placeholder=""
                :options="unitOption" />
            </n-form-item>
            <n-form-item label="" path="propertyType">
              <n-select v-model:value="housingValue.room" placeholder="" :options="houseType[0]" />
              <div style="width: 10px"></div>
              <n-select v-model:value="housingValue.hall" placeholder="" :options="houseType[1]" />
            </n-form-item>
            <n-form-item label="" path="housesType">
              <n-select v-model:value="housingValue.housesType" :loading="housingTypeLoading" placeholder=""
                :options="housingModelOptions" />
            </n-form-item>
            <n-form-item label="()" path="area">
              <n-input-number style="width:100%" v-model:value="housingValue.area" placeholder="" clearable />
            </n-form-item>
            <n-form-item label="">
              <n-switch v-model:value="housingValue.exquisite">
                <template #checked>
                  
                </template>
                <template #unchecked>
                  
                </template>
              </n-switch>
            </n-form-item>
            <n-form-item label="" path="">
              <n-input v-model:value="housingValue.remark" type="textarea" placeholder="" />
            </n-form-item>
          </n-grid-item>
        </n-grid>
      </n-form>
      <template #footer>
        <n-space justify="end">
          <n-button @click="showHousingModal = false"></n-button>
          <n-button :loading="housingSubmitLoading" type="primary" @click="housingSubmit" attr-type="button">
          </n-button>
        </n-space>
      </template>
    </n-modal>

    <!--  -->
    <n-modal :auto-focus="false" v-model:show="showImportPropertyModal" class="custom-card" preset="card"
      style="width: 600px" title="" transform-origin="center" size="huge" :bordered="false"
      :segmented="{content: 'soft',footer: 'soft'}">
      <n-form ref="showImportPropertyFormRef" label-placement="left" :label-width="120" size="medium">
        <n-grid cols="1">
          <n-grid-item>
            <n-form-item label="">
              <n-upload :show-file-list="true" accept=".xls,.xlsx" :action="global.api + '/housesExcelImport'" @finish="importPropertySubmit">
                <n-button></n-button>
              </n-upload>
            </n-form-item>
            <n-form-item label="">
               <a :href="global.api + '/housesExcelExport'"></a> 
            </n-form-item>
          </n-grid-item>
        </n-grid>
      </n-form>
      <template #footer>
        <n-space justify="end">
          <n-button @click="showImportPropertyModal = false"></n-button>
        </n-space>
      </template>
    </n-modal>


  </div>

</template>

<script setup>
//  vue
import { ref, reactive, computed, inject } from 'vue'
//  naive-ui 
import {
  NButton, NIcon, NGrid, NGridItem, NTree, NCard,
  NInput, NSelect, NSpace, NTable, NButtonGroup,
  NPagination, NModal, NForm, NFormItem, NInputNumber,
  NSwitch, useMessage, NSpin, useLoadingBar, NResult,
  NUpload
} from 'naive-ui'
//  axios
import axios from 'axios';
//  
import {
  Add16Regular as AddIcon,
  Edit16Regular as EditIcon,
  Delete16Regular as DeleteIcon,
  Search16Regular as SearchIcon,
  ArrowClockwise16Regular as ArrowClockwiseIcon
} from '@vicons/fluent'

//  Hook
import buildingHook from '../../hook/real_estate/housingmanagement/buildingHook'

//  Hook
import apartmentHook from '../../hook/real_estate/housingmanagement/apartmentHook'

//  Hook
import housingHook from '../../hook/real_estate/housingmanagement/housingHook'

//  Hook
import importPropertyHook from '../../hook/real_estate/housingmanagement/importPropertyHook'

// 
const global = inject('global');

// 
const message = useMessage();

//  Spin
const mainSpin = ref(false);

// 
const {
  buildingStatus, // 
  buildingFormRef, //  Form 
  showBuildingModal, // 
  buildingRules, //  Form 
  buildingValue, //  Form 
  buildingData, // 
  leftSpin, //  Spin
  buildingNode, // 
  unitNode, // 
  submitLoading, // 
  loadBuilding, //
  buildingSubmit, //  Form 
  nodeProps, // 
  deleteBuilding, // 
} = buildingHook(loadList)

// 
const {
  apartmentStatus, // 
  apartmentFormRef, //  Form 
  showApartmentModal, // 
  apartmentRules, //  Form 
  apartmentValue, //  Form 
  apartmentSubmitLoading, // 
  apartmentSubmit, //  Form 
  deleteApartment, // 
} = apartmentHook(buildingNode, loadBuilding, unitNode)

// 
const {
  housingModelStatus, // 
  housingFormRef, //  Form 
  showHousingModal, // 
  housingSubmitLoading, // 
  housingRules, //  Form 
  housingValue, //  Form 
  houseType, // 
  unitOption, // 
  unitOptionLoading, // 
  housingSubmit, // Form 
  openHousingModel, // 
  deleteHousing, // 
} = housingHook(unitNode, refreshList)

// 
const {
  showImportPropertyModal, // 
  importPropertySubmit, // 
  openImportProperty, // 
} = importPropertyHook(refreshList)


// 
const loadingBar = useLoadingBar()

// 
const houseData = ref([])
const houseDataDisplay = computed(() => {
  return (houseData.value.length === 0 || houseData.value.list.length === 0);
})

// 
const pageNo = ref(1)
const pages = ref(1)

// 
function pageNoUpdate(page) {
  loadList(unitNode.value, page)
}

// 
function loadList(option, page, more) {
  mainSpin.value = true // 
  // loadingBar.start()
  const params = {
    buildingNo: option.building.id,
    unitNo: option.id,
    page,
    size: global.pageSize
  }
  for (let key in more) {
    if (more[key] != null) {
      params[key] = more[key]
    }
  }

  axios({
    method: 'get',
    url: global.api + '/getAllHouses',
    params
  }).then(res => {
    // 
    houseData.value = res.data
    // 
    pageNo.value = res.data.pageNum
    // 
    pages.value = res.data.pages
    mainSpin.value = false // 
    // loadingBar.finish()
  }).catch(err => {
    // loadingBar.error()
  })
}

const statusOptions = [
  {
    label: "",
    value: null,
  },
  {
    label: "",
    value: 0,
  },
  {
    label: "",
    value: 1,
  }
]

const housingTypeLoading = ref(false)
const housingModelOptions = ref([])
const housingOptions = ref([
  {
    label: '',
    value: null
  }
])
// 
housingTypeLoading.value = true;
axios({
  method: 'get',
  url: global.api + '/getHousesTypeList'
}).then(res => {
  for (let type of res.data) {
    housingModelOptions.value.push({
      label: type.name,
      value: type.id
    })
    housingOptions.value.push({
      label: type.name,
      value: type.id
    })
  }
}).catch(err => {
  message.error("" + err)
}).finally(() => {
  housingTypeLoading.value = false;
})

// 
const housingNo = ref('')
const housingStatus = ref(null)
const housingType = ref(null)
// 
const findClick = () => {
  // refreshList()
  if (unitNode.value != null) {
    loadList(unitNode.value, 1, {
      housesNo: housingNo.value,
      status: housingStatus.value,
      housesType: housingType.value
    });
  } else {
    message.warning("")
  }
}
// 
function refreshList() {
  if (unitNode.value != null) {
    loadList(unitNode.value, pageNo.value, {
      housesNo: housingNo.value,
      status: housingStatus.value,
      housesType: housingType.value
    });
  } else {
    message.warning("")
  }
}

// 
const resetClick = () => {
  housingNo.value = ''
  housingStatus.value = null
  housingType.value = null
}



</script>

<script>
export default {
  name: 'HousingManagementLayout'
}
</script>

<style scoped>
.button-box>* {
  margin: 0 10px 10px 0;
}

.button-box {
  margin-top: 10px;
  margin-left: 10px;
}

.main {
  display: flex;
  flex-direction: row;
}

.left {
  width: 200px;
  margin-left: 10px;
}

.right {
  flex: 1;
  margin: 0 10px;
}
</style>


