import { ref, inject, watch } from 'vue'

import { useMessage, useDialog } from 'naive-ui'

import QueryString from 'qs'

import axios from 'axios'

export default (buildingNode, loadBuilding, unitNode) => {

    // 
    const global = inject("global")

    const message = useMessage();
    const dialog = useDialog();

    //  
    const apartmentStatus = ref(false); // false  true 
    const apartmentFormRef = ref(null);
    const showApartmentModal = ref(false) // 

    // 
    const apartmentSubmitLoading = ref(false);

    // 
    const apartmentRules = {
        id: {
            required: true,
            message: "",
            trigger: "blur"
        },
        totalNumberOfLayers: {
            type: "number",
            required: true,
            message: "",
            trigger: "blur"
        }
    }

    // 
    const apartmentValue = ref({
        id: "",
        totalNumberOfLayers: null,
        elevator: false,
        remark: ""
    })

    // 
    const apartmentSubmit = (e) => {
        e.preventDefault();
        apartmentFormRef.value?.validate((errors) => {
            if (!errors) {
                // 
                if (!apartmentStatus.value) { // 
                    addApartment()
                } else { // 
                    updateApartment();
                }
            } else {
                // 
            }
        });
    }

    watch(showApartmentModal, (newValue, oldValue) => {
        if (newValue) {
            apartmentValue.value = {
                id: "",
                totalNumberOfLayers: null,
                elevator: false,
                remark: ""
            }
            if (buildingNode.value != null && apartmentStatus.value) { // 
                if (unitNode.value != null) {
                    const unit = unitNode.value;
                    apartmentValue.value = {
                        id: unit.id,
                        totalNumberOfLayers: unit.totalNumberOfLayers,
                        elevator: unit.elevator,
                        remark: unit.remark,
                        key: unitNode.value.key.substring(1),
                    }
                } else {
                    message.warning("")
                    showApartmentModal.value = false
                }
            } else if (buildingNode.value != null) { // 

            } else {
                message.warning("")
                showApartmentModal.value = false
            }
        }
    })

    // 
    const deleteApartment = () => {
        const unit = unitNode.value
        if (unit != null) {
            dialog.warning({
                title: "!",
                content: "" + unit.label + "!",
                positiveText: "",
                negativeText: "",
                onPositiveClick: () => {
                    let delLoading = message.loading("")
                    axios({
                        method: 'delete',
                        url: global.api + '/deleteUnit/' + unit.key.substring(1)
                      }).then(res => {
                        if (res.data.success) {
                          loadBuilding();
                          message.success("")
                        } else {
                          message.warning("")
                        }
                      }).catch(err => {
                        message.error("" + err)
                      }).finally(() => {
                        delLoading.destroy();
                        delLoading = null;
                      })
                }
            });
        } else {
            message.warning("")
        }
    }

    // 
    const addApartment = () => {
        apartmentSubmitLoading.value = true
        console.log(unitNode);
        axios({
            method: 'post',
            url: global.api + '/addUnit',
            data: QueryString.stringify({
                buildingNo: buildingNode.value.id,
                unitNo: apartmentValue.value.id,
                totalFloors: apartmentValue.value.totalNumberOfLayers,
                elevator: apartmentValue.value.elevator ? 1 : 0,
                remark: apartmentValue.value.remark,
            })
        }).then(res => {
            if (res.data.success) {
                message.success("")
                loadBuilding();
                showApartmentModal.value = false
            } else {
                message.warning("")
            }
        }).catch(err => {
            message.error("" + err)
        }).finally(() => {
            apartmentSubmitLoading.value = false
        })
    }

    // 
    const updateApartment = () => {
        apartmentSubmitLoading.value = true
        axios({
            method: 'put',
            url: global.api + '/updateUnit',
            headers: {
                'Content-Type': 'application/json;charset=UTF-8'
            },
            data: {
                id: apartmentValue.value.key,
                buildingNo: unitNode.value.building.id,
                unitNo: apartmentValue.value.id,
                totalFloors: apartmentValue.value.totalNumberOfLayers,
                elevator: apartmentValue.value.elevator ? 1 : 0,
                remark: apartmentValue.value.remark
            }
        }).then(res => {
            if (res.data.data.updateStatus) {
                message.success("");
                loadBuilding();
                unitNode.value = null
                showApartmentModal.value = false
            } else {
                message.warning("")
            }
        }).catch(err => {
            message.error("" + err)
        }).finally(() => {
            apartmentSubmitLoading.value = false
        })
    }

    return {
        apartmentStatus, // 
        apartmentFormRef, //  Form 
        showApartmentModal, // 
        apartmentRules, //  Form 
        apartmentValue, //  Form 
        apartmentSubmitLoading, // 
        apartmentSubmit, //  Form 
        deleteApartment, // 
    }
}