import { ref, inject, computed } from 'vue'

import { useMessage, useDialog } from 'naive-ui'

import axios from 'axios'

import QueryString from 'qs'

export default (unitNode, refreshList) => {

    // 
    let tempHousing = null;

    // 
    const global = inject('global');

    const message = useMessage();
    const dialog = useDialog();

    //  
    const housingModelStatus = ref(false); // false  true 
    const housingFormRef = ref(null);
    const showHousingModal = ref(false) // 

    // 
    const housingSubmitLoading = ref(false);

    // 
    const housingRules = {
        housesNo: {
            required: true,
            message: "",
            trigger: "blur"
        },
        floor: {
            required: true,
            type: "number",
            message: "",
            trigger: "blur"
        },
        propertyType: {
            required: true,
            validator(rule, value) {
                if (value.length !== 4) {
                    return new Error("");
                }
                return true;
            },
            trigger: "blur"
        },
        housesType: {
            required: true,
            type: 'number',
            message: "",
            trigger: "blur"
        },
        area: {
            required: true,
            type: "number",
            message: "",
            trigger: "blur"
        },
    }

    // 
    const housingValue = ref({
        housesNo: "", // 
        floor: null, // 
        room: null, // 
        hall: null, // 
        propertyType: computed({
            set(value) {
                housingValue.value.room = value.substring(0, 2)
                housingValue.value.hall = value.substring(2)
            },
            get() {
                return housingValue.value.room + housingValue.value.hall
            }
        }), // 
        housesType: null, // 
        area: null, // 
        exquisite: false, // 
        remark: "", // 
    })

    const houseType = [
        [{
            value: "",
            label: "",
        }, {
            value: "",
            label: "",
        }, {
            value: "",
            label: "",
        }, {
            value: "",
            label: "",
        }, {
            value: "",
            label: "",
        }, {
            value: "",
            label: "",
        }, {
            value: "",
            label: "",
        }, {
            value: "",
            label: "",
        }], [{
            value: "",
            label: "",
        }, {
            value: "",
            label: "",
        }, {
            value: "",
            label: "",
        }, {
            value: "",
            label: "",
        }, {
            value: "",
            label: "",
        }, {
            value: "",
            label: "",
        }, {
            value: "",
            label: "",
        }, {
            value: "",
            label: "",
        }]
    ]

    // 
    const unitOptionLoading = ref(false)
    // 
    const unitOption = ref([])

    // 
    const housingSubmit = (e) => {
        e.preventDefault();
        housingFormRef.value?.validate((errors) => {
            if (!errors) {
                // 
                if (!housingModelStatus.value) { // 
                    addHousing()
                } else { // 
                    updateHousing()
                }
            } else {
                // 
            }
        });
    }

    /**
     * 
     */
    const addHousing = () => {
        housingSubmitLoading.value = true
        axios({
            method: 'post',
            url: global.api + "/addHouses",
            data: QueryString.stringify({
                buildingNo: unitNode.value.building.id,
                unitNo: unitNode.value.id,
                housesNo: housingValue.value.housesNo,
                floor: housingValue.value.floor,
                propertyType: housingValue.value.propertyType,
                housesType: housingValue.value.housesType,
                area: housingValue.value.area,
                status: 0,
                exquisite: housingValue.value.exquisite ? 1 : 0,
                remark: housingValue.value.remark,
            })
        }).then(res => {
            if (res.data.success) {
                message.success('')
                showHousingModal.value = false
                refreshList()
            } else {
                message.warning('')
            }
        }).catch(err => {
            message.error('' + err)
        }).finally(() => {
            housingSubmitLoading.value = false
        })
    }

    /**
     * 
     */
    const updateHousing = () => {
        housingSubmitLoading.value = true
        axios({
            method: 'put',
            url: global.api + '/updateHouses',
            data: QueryString.stringify({
                id: tempHousing.id,
                buildingNo: unitNode.value.building.id,
                unitNo: housingValue.value.unitNo,
                housesNo: housingValue.value.housesNo,
                floor: housingValue.value.floor,
                propertyType: housingValue.value.propertyType,
                housesType: housingValue.value.housesType,
                area: housingValue.value.area,
                status: tempHousing.status,
                exquisite: housingValue.value.exquisite ? 1 : 0,
                remark: housingValue.value.remark,
            })
        }).then(res => {
            if (res.data.data.updateStatus) {
                message.success("")
                showHousingModal.value = false
                refreshList()
            } else {
                message.warning("")
            }
        }).catch(err => {
            message.error("" + err)
        }).finally(() => {
            housingSubmitLoading.value = false
        })
    }

    /**
     * 
     * @param {*} housing 
     */
    const deleteHousing = (housing) => {
        dialog.warning({
            title: "!",
            content: "" + housing.housesNo + "!",
            positiveText: "",
            negativeText: "",
            onPositiveClick: () => {
                let delLoading = message.loading("")
                axios({
                    method: 'delete',
                    url: global.api + '/deleteHouses/' + housing.id
                }).then(res => {
                    if (res.data.success) {
                        message.success("")
                        refreshList();
                    } else {
                        message.warning("")
                    }
                }).catch(err => {
                    message.error("" + err)
                }).finally(() => {
                    delLoading.destroy();
                    delLoading = null;
                })
            }
        });
    }

    /**
     * 
     * @param {*} status false  false  
     */
    const openHousingModel = (status, housing) => {
        // 
        if (unitNode.value == null) {
            message.warning("")
            return;
        }

        // 
        housingValue.value = {
            housesNo: "", // 
            floor: null, // 
            room: null, // 
            hall: null, // 
            propertyType: computed({
                set(value) {
                    housingValue.value.room = value.substring(0, 2)
                    housingValue.value.hall = value.substring(2)
                },
                get() {
                    return housingValue.value.room + housingValue.value.hall
                }
            }), // 
            housesType: null, // 
            area: null, // 
            exquisite: false, // 
            remark: "", // 
        }

        if (status) { // 

            unitOption.value = []

            unitOptionLoading.value = true
            axios({
                method: 'get',
                url: global.api + '/getAllByBuildingNoUnits/' + unitNode.value.building.id,
            }).then(res => {
                for (let unit of res.data) {
                    unitOption.value.push({
                        label: unit.unitNo + "",
                        value: unit.unitNo
                    })
                }
            }).catch(err => {
                console.log("" + err);
            }).finally(() => {
                unitOptionLoading.value = false
            })


            tempHousing = housing;

            housingValue.value = {
                housesNo: housing.housesNo, // 
                floor: housing.floor, // 
                unitNo: housing.unit.unitNo,
                room: housing.propertyType.substring(0, 2), // 
                hall: housing.propertyType.substring(2), // 
                propertyType: computed({
                    set(value) {
                        housingValue.value.room = value.substring(0, 2)
                        housingValue.value.hall = value.substring(2)
                    },
                    get() {
                        return housingValue.value.room + housingValue.value.hall
                    }
                }), // 
                housesType: housing.housesType.id, // 
                area: housing.area, // 
                exquisite: housing.exquisite === 1, // 
                remark: housing.remark, // 
            }
        }

        showHousingModal.value = true;
        housingModelStatus.value = status;
    }

    return {
        housingModelStatus, // 
        housingFormRef, //  Form 
        showHousingModal, // 
        housingSubmitLoading, // 
        housingRules, //  Form 
        housingValue, //  Form 
        houseType, // 
        unitOption, // 
        unitOptionLoading, // 
        housingSubmit, // Form 
        openHousingModel, // 
        deleteHousing, // 
    }

}