import { ref, reactive, inject, watch } from 'vue'

import { useMessage, useDialog } from 'naive-ui'

import axios from 'axios'

import QueryString from 'qs'

export default (loadList) => {

  // 
  const global = inject('global');

  const message = useMessage();
  const dialog = useDialog();

  const leftSpin = ref(false)

  // 
  const buildingNode = ref(null);
  const unitNode = ref(null);

  //  
  const buildingStatus = ref(false); // false  true 
  const buildingFormRef = ref(null);
  const showBuildingModal = ref(false) // 

  // 
  const submitLoading = ref(false);

  // 
  const buildingRules = {
    id: {
      required: true,
      message: "",
      trigger: "blur"
    },
    name: {
      required: true,
      message: "",
      trigger: "blur"
    },
    area: {
      required: true,
      message: "",
      trigger: "blur"
    },
  }

  // 
  const buildingValue = ref({
    id: "",
    name: "",
    area: "",
    remark: "",
  })

  // 
  const buildingSubmit = (e) => {
    e.preventDefault();
    buildingFormRef.value?.validate((errors) => {
      if (!errors) {
        // 
        if (!buildingStatus.value) { // 
          addBuilding(); // 
        } else { // 
          updateBuilding();
        }
      } else {
        // 
      }
    });
  }

  // 
  const buildingData = reactive([])

  /* 
  
  [{
      "label": "1",
      "type": 0,
      "key": "403626",
      "children": [
        {
          "label": "1",
          "type": 1,
          "key": "40362610"
        },
        {
          "label": "2",
          "type": 1,
          "key": "40362611"
        },
        {
          "label": "6",
          "type": 1,
          "key": "40362612"
        },
        {
          "label": "12",
          "type": 1,
          "key": "40362613"
        }
      ]
    }, {
      "label": "2",
      "type": 0,
      "key": "403627",
      "children": [
        {
          "label": "1",
          "type": 1,
          "key": "40362614"
        },
        {
          "label": "2",
          "type": 1,
          "key": "40362615"
        },
        {
          "label": "6",
          "type": 1,
          "key": "40362616"
        },
        {
          "label": "12",
          "type": 1,
          "key": "40362617"
        }
      ]
    }, , {
      "label": "3",
      "type": 0,
      "key": "403628",
      "children": [
        {
          "label": "1",
          "type": 1,
          "key": "40362618"
        },
        {
          "label": "2",
          "type": 1,
          "key": "40362619"
        },
        {
          "label": "6",
          "type": 1,
          "key": "40362620"
        },
        {
          "label": "12",
          "type": 1,
          "key": "40362621"
        }
      ]
    }]
  */

  // 
  leftSpin.value = true
  // 
  function loadBuilding() {
    buildingData.length = 0

    axios({
      method: 'get',
      url: global.api + '/getAllBuildings',
    }).then(res => {
      for (let building of res.data) {
        const tempData = reactive({
          "id": building.buildingNo,
          "label": building.name,
          "type": 0,
          "key": "L" + building.id,
          "area": building.area,
          "remark": building.remark,
          "children": []
        })

        axios({
          method: 'get',
          url: global.api + '/getAllByBuildingNoUnits/' + building.buildingNo,
        }).then(res => {
          for (let unit of res.data) {
            tempData.children.push({
              "id": unit.unitNo,
              "label": unit.unitNo + "",
              "type": 1,
              "totalNumberOfLayers": unit.totalFloors,
              "elevator": unit.elevator === 1,
              "key": "D" + unit.id,
              "building": {
                "id": building.buildingNo,
                "label": building.name,
                "type": 0,
                "key": "L" + building.id,
                "area": building.area,
                "remark": building.remark,
              }
            })
          }
          leftSpin.value = false
        }).catch(err => {
          message.error('' + err)
          leftSpin.value = false
        })
        buildingData.push(tempData);
      }
    }).catch(err => {
      leftSpin.value = false
      message.error('' + err, { duration: 5000 })
    })
  }
  loadBuilding()

  watch(showBuildingModal, (newValue, oldValue) => {
    if (newValue) {
      buildingValue.value = {
        id: "",
        name: "",
        area: "",
        remark: "",
      }
    }
    if (newValue && buildingStatus.value) {
      if (buildingNode.value != null) {
        const buildingInfo = buildingNode.value;
        buildingValue.value = {
          id: buildingInfo.id,
          name: buildingInfo.label,
          area: buildingInfo.area.toString(),
          remark: buildingInfo.remark,
          key: buildingInfo.key.substring(1)
        }
      } else {
        message.warning("")
        showBuildingModal.value = false
      }
    }
  })

  // 
  function nodeProps({ option }) {
    return {
      onClick() {
        if (option.type === 0) { // 
          buildingNode.value = option
        } else if (option.type === 1) { // 
          buildingNode.value = option.building
          unitNode.value = option
          loadList(option, 1);
        }
      }
    };
  }

  // 
  const deleteBuilding = () => {
    if (buildingNode.value != null) {
      dialog.warning({
        title: "!",
        content: "" + buildingNode.value.label + "!",
        positiveText: "",
        negativeText: "",
        onPositiveClick: () => {
          let delLoading = message.loading("")
          axios({
            method: 'delete',
            url: global.api + '/deleteBuilding/' + buildingNode.value.key.substring(1)
          }).then(res => {
            if (res.data.success) {
              loadBuilding();
              message.success("")
            } else {
              message.warning("")
            }
          }).catch(err => {
            message.error("" + err)
          }).finally(() => {
            delLoading.destroy();
            delLoading = null;
          })
        }
      });
    } else {
      message.warning("")
    }
  }

  // 
  const addBuilding = () => {
    submitLoading.value = true
    axios({
      method: 'post',
      url: global.api + '/addBuilding',
      data: QueryString.stringify({
        buildingNo: buildingValue.value.id,
        name: buildingValue.value.name,
        area: buildingValue.value.area,
        remark: buildingValue.value.remark
      })
    }).then(res => {
      if (res.data.success) {
        message.success("");
        loadBuilding()
        showBuildingModal.value = false
      } else {
        message.warning("");
      }
    }).catch(err => {
      message.error("" + err)
    }).finally(() => {
      submitLoading.value = false
    })
  }

  // 
  const updateBuilding = () => {
    submitLoading.value = true
    axios({
      method: 'put',
      url: global.api + '/updateBuilding',
      headers: {
        'Content-Type': 'application/json;charset=UTF-8'
      },
      data: {
        "area": buildingValue.value.area,
        "buildingNo": buildingValue.value.id,
        "id": buildingValue.value.key,
        "name": buildingValue.value.name,
        "remark": buildingValue.value.remark
      }
    }).then(res => {
      if (res.data.data.updateStatus) {
        message.success("");
        loadBuilding();
        buildingNode.value = null
        showBuildingModal.value = false
      } else {
        message.warning("")
      }
    }).catch(err => {
      message.error("" + err)
    }).finally(() => {
      submitLoading.value = false
    })
  }

  return {
    buildingStatus, // 
    buildingFormRef, //  Form 
    showBuildingModal, // 
    buildingRules, //  Form 
    buildingValue, //  Form 
    buildingData, // 
    leftSpin, //  Spin
    buildingNode, // 
    unitNode, // 
    submitLoading, // 
    loadBuilding, //
    buildingSubmit, //  Form 
    nodeProps, // 
    deleteBuilding, // 
  }
}




