import { ref } from 'vue'

import { useMessage } from 'naive-ui'

export default (refreshList) => {

    // 
    const message = useMessage();

    // 
    const showImportPropertyModal = ref(false)

    /**
     * 
     */
    const importPropertySubmit = ({
        file,
        event
    }) => {
        const res = JSON.parse((event?.target).response);
        if (res.success) {
            message.success("" + res.data.excelCountTotal + " " + res.data.successCount + " " + res.data.failedCount + " ",{duration: 3000})
            showImportPropertyModal.value = false
            refreshList()
        } else {
            message.warning("")
        }
        return file;
    };

    /**
     * 
     */
    const importProperty = () => {
        
    }

    /**
     * 
     */
    const openImportProperty = () => {
        showImportPropertyModal.value = true
    }

    return {
        showImportPropertyModal, // 
        importPropertySubmit, // 
        openImportProperty, // 
    }

}